import { useState } from "react";
import { useTranslation } from "react-i18next";
import { NodeTableData } from "../../../typings";
import Progress from "../../../ui-components/commons/progress/Progress.component";
import { DropdownSelectCell, EditableCell } from "../../../ui-components/commons/table";
import DataTable from "../../../ui-components/datatables/DataTable.component";
import CirclePlusIcon from "../../../ui-components/icons/CirclePlus.icon";
import { getNodeCompletionRate, getStatus, sortDataTableProjects } from "../../../utils";
export interface CellUpdateProp {
    id: number | string;
    node: { [key: string]: any };
};
export interface StatusOption { id?: string; label?: string; [key: string]: string; };
export interface NodeDataTableProps {
    data: Array<NodeTableData>;
    onUpdateNode: (props: CellUpdateProp) => void;
    onUpdateCell: (props: CellUpdateProp) => void;
    statusOptions?: Array<StatusOption>;
    onAddNewNode?: (props: CellUpdateProp) => void;
    newColumns?: Array<string>;
    onNewColumnBlur?: (name: string) => void;
    onAddNewColumn?: (name: string) => void;
    newColumnName?: string;
    onNewRowClick?: (e: any) => void;
    [key: string]: any;
}
const ProjectDataTable = ({
    data,
    onUpdateNode,
    onUpdateCell,
    onAddNewNode,
    statusOptions,
    newColumns,
    onNewColumnBlur,
    onAddNewColumn,
    newColumnName,
    onNewRowClick,
    ...props
}: NodeDataTableProps) => {
    const { t } = useTranslation();
    const [newColumnClick, setNewColumnClick] = useState(false);
    const handleAddNewNode = ({ id, node }: CellUpdateProp) => {
        if (onAddNewNode) {
            onAddNewNode({ id, node });
        }
    }
    const handleBlurNewColumn = (name: string) => {
        if (onNewColumnBlur) {
            onNewColumnBlur(name);
        }
        setNewColumnClick(false);
    }
    const handleAddNewColumn = (name: string) => {
        if (onAddNewColumn && name?.trim()) {
            onAddNewColumn(name);
        }
        setNewColumnClick(false);
    }
    const handleNewRowClick = (e: any) => {
        if (onNewRowClick) {
            onNewRowClick(e);
        }
    }
    return (
        <DataTable
            {...props}
            data={[...data].sort(sortDataTableProjects)}
            meta={{
                name: {
                    displayName: t('project-name'),
                    render: (p, index) => (
                        <div style={{
                            padding: 0,
                            background: '#fff',
                            borderTopLeftRadius: index === 0 ? 5 : 0,
                            borderTopRightRadius: index === 0 ? 5 : 0,
                        }}>
                            <EditableCell
                                style={{ textAlign: 'right' }}
                                onEnter={(val) => onUpdateNode({ id: p.id, node: { name: val }})}
                                defaultValue={p.name}
                            />
                        </div>
                    )
                },
                creator: {
                displayName: t('project-manager'),
                render: (p, index) => (
                    <div  style={{
                            padding: '0 5px',
                            background: '#fff',
                            borderTopLeftRadius: index === 0 ? 5 : 0,
                            borderTopRightRadius: index === 0 ? 5 : 0,
                        }}
                        title={`${p.creator?.firstName ?? ''} ${p.creator?.lastName ?? ''}`}
                    >
                        <EditableCell
                            readonly
                            style={{ textAlign: 'right' }}
                            onEnter={(val) => console.log(val)}
                            defaultValue={`${p.creator?.firstName ?? ''} ${p.creator?.lastName ?? ''}`}
                        />
                    </div>
                )
                },
                progression: {
                    displayName: t('progression'),
                    render: (p, index, extra) => {
                        const progress = getNodeCompletionRate(p?.tree as any);
                        return (
                            <div style={{
                                display: 'flex',
                                background: '#fff',
                                borderBottom: '2px solid #fff',
                                borderTopLeftRadius: index === 0 ? 5 : 0,
                                borderTopRightRadius: index === 0 ? 5 : 0,
                            }}>
                                <Progress color="#ED6E40" progress={Math.round(progress * 100)} />
                            </div>
                        )
                    }
                },
                status: {
                    displayName: t('status'),
                    render: (p: any) => {
                        const st = getStatus((p?.tree ? p?.tree?.attributes?.status : p?.attributes?.status ));
                        return (
                            <DropdownSelectCell
                                options={statusOptions}
                                selected={st.status}
                                onChange={(status) => onUpdateCell({ id: p.id, node: { id: p?.tree?.attributes?.id, status }})}
                                color={st.color}
                            />
                        )
                    }
                },
            }}
            treeMeta={{
                status: {
                    render: (pr, p) => {
                        const st = getStatus((p?.tree ? p?.tree?.attributes?.status : p?.attributes?.status));
                        return (
                            <DropdownSelectCell
                                options={statusOptions}
                                selected={st.status}
                                onChange={(status) => onUpdateCell({ id: pr.id, node: { id: p?.attributes?.id, status }})}
                                color={st.color}
                            />
                        )
                    }
                },
                none: {
                    render: (p, node) => <CirclePlusIcon style={{ height: 20, width: 20 }} onClick={() => handleAddNewNode({ id: p.id, node })} />
                }
            }}
            newColumn={{
                renderData: (index) => {
                    return <div style={{
                            display: 'flex',
                            background: '#fff',
                            borderTopLeftRadius: index === 0 ? 5 : 0,
                            borderTopRightRadius: index === 0 ? 5 : 0,
                    }}>
                        <EditableCell readonly />
                    </div>
                },
                renderHeader: () => newColumnClick ? (
                    <EditableCell
                        autoFocus
                        style={{ textAlign: 'right' }}
                        onBlur={handleBlurNewColumn}
                        onEnter={handleAddNewColumn}
                        defaultValue={newColumnName}
                    />
                ) : <CirclePlusIcon style={{ height: 20, width: 20 }} onClick={() => setNewColumnClick(true)} />
            }}
            newRow={{
                render: (d, index, len) => {
                    return (
                        <div style={{
                            display: 'flex',
                            background: '#fff',
                            borderBottomLeftRadius: index === 0 ? 5 : 0,
                            borderBottomRightRadius: index === len - 1 ? 5 : 0,
                    }}>
                        { index === 0 ? (
                            <span style={{ padding: '0 10px' }} title={t('use-map-to-select-location')}>
                                <CirclePlusIcon style={{ height: 20, width: 20, marginTop: -5 }} onClick={handleNewRowClick} />
                            </span>
                        ) :  <span style={{ visibility: 'hidden' }}>h</span> }
                    </div>
                    )
                }
            }}
            appendColums={newColumns}
        />
    );
};

export default ProjectDataTable;
