import { useEffect } from "react";
import { useTranslation } from "react-i18next";
import './kanban.scss';
export interface KanbanColunHeaderProp {
    title?: string;
    color?: string;
    iconName?: string;
    id?: number | string;
    label?: string;
    cards?: Array<any>;
};

const KanbanColumnHeader = ({ color, label, cards }: KanbanColunHeaderProp) => {
    const { t } = useTranslation();
    useEffect(() => {
        const columnEl = document.body.querySelector(`div[data-rbd-draggable-id="column-draggable-${label}"]`) as HTMLElement;
        if (columnEl) {
            columnEl.style.backgroundColor = `rgba(${color}, 0.5)`;
        }
    }, [])
    return (
        <div
            id={`kanban-column-header-${label}`}
            className="kanban-colum-header"
            style={{
                marginBottom: 5,
                color: `rgb(${color})`,
                padding: 5,
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center'
            }}
        >
          {`${t(label)} (${cards?.length})`}
          <button style={{ backgroundColor: `rgba(${color}, 0.3)`}} className="kanban-colum-header-btn" type="button">
            <span style={{ backgroundColor: `rgb(${color})` }}/>
            <span style={{ backgroundColor: `rgb(${color})` }}/>
            <span style={{ backgroundColor: `rgb(${color})` }}/>
          </button>
        </div>
    );
};

export default KanbanColumnHeader;
