export interface EditableCellPropType {
    onEnter?: (text: string) => void;
    onBlur?: (text: string) => void;
    style?: any;
    defaultValue?: string | number;
    className?: string;
    readonly?: boolean;
    [key: string]: any;
}
const EditableCell = ({  onEnter, style, className, onBlur, defaultValue, readonly, ...props }: EditableCellPropType) => {
    const handleBlur = (e: any) => {
        const val = e?.target?.value;
        if (onBlur && val?.toString() !== defaultValue?.toString()) {
            onBlur(e?.target?.value);
        }
    }
    const handleEnter = (e: any) => {
        const val = e?.target?.value;
        if (onEnter) {
            if (e?.key && e?.key?.toLowerCase() === 'enter') {
                if (val?.toString() !== defaultValue?.toString()) {
                    onEnter(val);
                }
            } else if (e?.key && e?.key?.toLowerCase() === 'tab') {
                if (val?.toString()?.trim()) {
                    onEnter(val);
                }
            }
        }
        
    }
    return (
        <input style={{
            ...{
                width: '100%',
                background: 'transparent',
                border: 'none',
                borderBottom: '1px solid transparent',
                outline: 'none',
                margin: 0,
                lineHeight: 'inherit',
                fontFamily: 'inherit',
                fontSize: 'inherit',
            }, ...(
                style ?? {}
            )
        }}
        onKeyDown={handleEnter}
        onBlur={handleBlur}
        defaultValue={defaultValue}
        readOnly={readonly}
        {...props}
        className={`editable-cell-sb ${ className ?? '' }`}
        />
    )
}

export default EditableCell;
