import { useEffect, useState } from "react";
import { RawNodeDatum } from "react-d3-tree/lib/types/common";
import { useTranslation } from "react-i18next";
import { NodeTableData } from "../../../typings";
import Progress from "../../../ui-components/commons/progress/Progress.component";
import { DropdownSelectCell, EditableCell } from "../../../ui-components/commons/table";
import DataTable from "../../../ui-components/datatables/DataTable.component";
import TreeDataTable from "../../../ui-components/datatables/TreeDataTable.component";
import CirclePlusIcon from "../../../ui-components/icons/CirclePlus.icon";
import { getStatus, newTreeNode, sortDataTableProjects } from "../../../utils";
export interface CellUpdateProp {
    id: number | string;
    node: { [key: string]: any };
};
export interface StatusOption { id?: string; label?: string; [key: string]: string; };
export interface NodeDataTableProps {
    data?: Array<NodeTableData>;
    node: RawNodeDatum;
    onUpdateNode: (props: CellUpdateProp) => void;
    onUpdateCell: (props: CellUpdateProp) => void;
    statusOptions?: Array<StatusOption>;
    onAddNewNode?: (props: CellUpdateProp) => void;
    newColumns?: Array<string>;
    onNewColumnBlur?: (name: string) => void;
    onAddNewColumn?: (name: string) => void;
    newColumnName?: string;
    onNewRowClick?: (e: any) => void;
    selectedProjectId?: number | string;
    [key: string]: any;
}
const NodeDataTable = ({
    data,
    node,
    onUpdateNode,
    onUpdateCell,
    onAddNewNode,
    statusOptions,
    newColumns,
    onNewColumnBlur,
    onAddNewColumn,
    newColumnName,
    onNewRowClick,
    selectedProjectId,
    ...props
}: NodeDataTableProps) => {
    const { t } = useTranslation();
    const [newColumnClick, setNewColumnClick] = useState(false);
    const [nodeNames, setNodeNames] = useState<{[key: string]: any}>({});
    const [nodeData, setNodeData] = useState<{[key: string]: any}>({});
    const [childNodes, setChildNodes] = useState<Array<any>>([]);
    const handleAddNewNode = ({ id, node }: CellUpdateProp) => {
        if (onAddNewNode) {
            onAddNewNode({ id, node });
        }
    }
    const handleBlurNewColumn = (name: string) => {
        if (onNewColumnBlur) {
            onNewColumnBlur(name);
        }
        setNewColumnClick(false);
    }
    const handleAddNewColumn = (name: string) => {
        if (onAddNewColumn && name?.trim()) {
            onAddNewColumn(name);
        }
        setNewColumnClick(false);
    }
    const handleNewRowClick = (e: any) => {
        setChildNodes((prev) => ([...prev, { ...newTreeNode, attributes: { ...newTreeNode.attributes, id: (new Date()).getTime() * Math.random(), nodeType: 'libre' }}]))
    }
    useEffect(() => {
        setChildNodes([...(node?.children ?? [])].sort(sortDataTableProjects));
    }, [node]);
    return (
        <TreeDataTable
            {...props}
            data={childNodes}
            meta={{
                elements: {
                    displayName: t('elements'),
                    render: (p: any, index) => {
                        return (
                            <div style={{
                                display: 'flex',
                                background: '#fff',
                                borderTopLeftRadius: index === 0 ? 5 : 0,
                                borderTopRightRadius: index === 0 ? 5 : 0,
                        }}>
                            <EditableCell
                                style={{ textAlign: 'right' }}
                                onEnter={(val) => onUpdateCell({ id: selectedProjectId, node: { id: p?.attributes?.id, name: val }})}
                                onChange={(e: any) => setNodeNames((prev) => ({ ...prev, [p?.attributes?.id]: e?.target?.value }))}
                                value={nodeNames[p?.attributes?.id] ?? p.name}
                                title={p?.attributes?.data}
                            />
                        </div>
                        )
                    }
                },
            }}
            treeMeta={{
                data: {
                    render: (pr, p, index) => {
                        return (
                            <div style={{
                                display: 'flex',
                                background: '#fff',
                                borderTopLeftRadius: index === 0 ? 5 : 0,
                                borderTopRightRadius: index === 0 ? 5 : 0,
                        }}>
                            <EditableCell
                                autoFocus
                                style={{ textAlign: 'right' }}
                                onEnter={(val) => onUpdateCell({ id: selectedProjectId, node: { id: p?.attributes?.id, data: val }})}
                                onChange={(e: any) => setNodeData((prev) => ({ ...prev, [p?.attributes?.id]: e?.target?.value }))}
                                value={nodeData[p?.attributes?.id] ?? (p?.attributes?.data || '')}
                            />
                         </div>   
                        )
                    }
                },
                empty: {
                    render: (p, vNode) => <CirclePlusIcon style={{ height: 20, width: 20 }} onClick={() => handleAddNewNode({ id: selectedProjectId, node: { ...vNode, parentNodeId: p?.attributes?.id } })} />
                },
                none: {
                    render: (p, vNode, index) => {
                        return (
                            <div style={{
                                display: 'flex',
                                background: '#eee',
                                borderTopLeftRadius: index === 0 ? 5 : 0,
                                borderTopRightRadius: index === 0 ? 5 : 0,
                        }}>
                            <EditableCell
                                onEnter={(val) => handleAddNewNode({ id: selectedProjectId, node: { ...vNode, data: val, parentNodeId: p?.attributes?.id } })}
                            />
                        </div>
                        )
                    }
                },
            }}
            newColumn={{
                renderData: (index) => {
                    return <div style={{
                            display: 'flex',
                            background: '#fff',
                            borderTopLeftRadius: index === 0 ? 5 : 0,
                            borderTopRightRadius: index === 0 ? 5 : 0,
                    }}>
                        <EditableCell readonly />
                    </div>
                },
                renderHeader: () => newColumnClick ? (
                    <EditableCell
                        autoFocus
                        style={{ textAlign: 'right' }}
                        onBlur={handleBlurNewColumn}
                        onEnter={handleAddNewColumn}
                        defaultValue={newColumnName}
                    />
                ) : <CirclePlusIcon style={{ height: 20, width: 20 }} onClick={() => setNewColumnClick(true)} />
            }}
            newRow={{
                render: (d, index, len) => {
                    return (
                        <div style={{
                            display: 'flex',
                            background: '#fff',
                            borderBottomLeftRadius: index === 0 ? 5 : 0,
                            borderBottomRightRadius: index === len - 1 ? 5 : 0,
                    }}>
                        { index === 0 ? (
                            <span style={{ padding: '0 10px' }} title={t('use-map-to-select-location')}>
                                <CirclePlusIcon style={{ height: 20, width: 20, marginTop: -5 }} onClick={handleNewRowClick} />
                            </span>
                        ) :  <span style={{ visibility: 'hidden' }}>h</span> }
                    </div>
                    )
                }
            }}
            appendColums={newColumns}
        />
    );
};

export default NodeDataTable;
