import { useSelector } from "../../../hooks";
import { RepresentationProps } from "../../../typings";
import KanbanComponent from "../../../ui-components/kanban/Kanban.component";

const KanbanView = ({ activeNode, projects, projectId, ...props }: RepresentationProps) => {
    const { projectNodeStatuses } = useSelector(state => state);
    const columns = projectNodeStatuses?.payload?.filter(s => ['todo', 'in-progress', 'done', 'delivered'].includes(s.label));
    return <KanbanComponent {...props} kanban={{ columns, data: activeNode ? activeNode?.children : projects }} />;
};

export default KanbanView;